import React from 'react';
import {Button} from 'react-bootstrap'
import image24 from '../Images/image 24.png'
import group5 from '../Images/Group 5.png'
import group6 from '../Images/Group 6.png'
import Rec235 from '../Images/Rectangle 235.png'

const Support = () => {
  return (
    <div  style={{paddingTop:"4rem", position:"relative"}}>
     <div className='text-center'>
     <h1>
      Supported by real people
      </h1>
      <p style={{color:"#94A2B3"}}>Our team of Happiness Engineers works remotely from 58 countries providing <br /> customer support across multiple time zones.</p>
      <img style={{width:"70vw", zIndex:"6"}} src={image24} alt="" />
      <img style={{width:"85vw" ,zIndex:"-1", position:"absolute", left:"7vw", top:"34vh"}} src={Rec235} alt="" />

      <img style={{width:"20vw" ,position:"absolute", top:"50vh", left:"5vw", zIndex:"-2" }} src={group5} alt="" />
      <img style={{ width:"20vw",position:"absolute", top:"45vh", right:"5vw", zIndex:"-3" }} src={group6} alt="" />
     </div>

      <div className='d-flex' style={{height:"250px", justifyContent:"center", alignItems:"center", color:"white", background:"#5F37EF", zIndex:"6"}}>
          <p style={{padding:"0 5rem", fontSize:"36px"}}>WooCommerce - the most customizable <br /> eCommerce platform for building your online business.</p>
          <Button style={{backgroundColor:"transparent", borderRadius:"30px", padding:"1rem 2rem", borderColor:"white", height:"60px"}}>
            GET STARTED
          </Button>
      </div>
    </div>
  );
}

export default Support;
