import React from 'react';
import {Navbar, Container, NavDropdown, Nav, Button} from 'react-bootstrap'
import logo from '../Images/LOGO.png'

const AppNavbar = () => {
  return (
    <div>
      <Navbar collapseOnSelect expand="lg" bg="light" variant="light">
        
        <Navbar.Brand style={{padding:"0 5rem"}} href="/"> <img src={logo} alt="" /></Navbar.Brand>
        <Navbar.Toggle aria-controls="responsive-navbar-nav" />
        <Navbar.Collapse id="responsive-navbar-nav">
            <Nav className="me-auto">
            <Nav.Link style={{color:"#272D4E"}} className='px-3' href="sell">Sell</Nav.Link>
            <Nav.Link style={{color:"#272D4E"}} className='px-3' href="marketplace">Marketplace</Nav.Link>
            <Nav.Link style={{color:"#272D4E"}} className='px-3' href="community">Community</Nav.Link>
            <Nav.Link style={{color:"#272D4E"}} className='px-3' href="develop">Develop</Nav.Link>
            <Nav.Link style={{color:"#272D4E"}} className='px-3' href="resources">Resources</Nav.Link>
            </Nav>
            <Nav>
            <Nav.Link style={{color:"#272D4E"}} className='mx-2' href="login">Log in</Nav.Link>
            <Button className='mx-2' style={{backgroundColor:"#7854F7"}}>Get started</Button>
            <Nav.Link style={{paddingRight:"4rem"}}><i style={{color:"black"}} class="fa fa-search"></i></Nav.Link>
            </Nav>
        </Navbar.Collapse>
        
        </Navbar>
    </div>
  );
}

export default AppNavbar;
