import React from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import photo1 from '../Images/Group 22.png'
import photo2 from '../Images/Group 23.png'
import photo3 from '../Images/Group 24.png'

const Features = () => {
  return (
    <div>
      <h1 className='text-center'>
      Your eCommerce <br /> made simple
      </h1>

     <Container>
     <Row>
          <Col sm={12} md={4} lg={4}>
              <img style={{width:"100%"}} src={photo1} alt="" />
              <div style={{padding:"0 2.5rem", position:"relative", top:"-125px"}}>
              <h2>All You Need to Start</h2>
              <p>Add WooCommerce plugin to any WordPress site and set up a new store in minutes.</p>
              <a style={{textDecoration:"none"}} href="/">Ecommerce for Wordpress ›</a>
              </div>
          </Col>

          <Col sm={12} md={4} lg={4}>
              <img style={{width:"100%"}} src={photo2} alt="" />
              <div style={{padding:"0 2.5rem", position:"relative", top:"-125px"}}>
              <h2>Customize and Extend</h2>
              <p>From subscriptions to gym classes to luxury cars, WooCommerce is fully customizable.</p>
              <a style={{textDecoration:"none"}} href="/">Browse Extensions ›</a>
              </div>
          </Col>

          <Col sm={12} md={4} lg={4}>
              <img style={{width:"100%"}} src={photo3} alt="" />
              <div style={{padding:"0 2.5rem", position:"relative", top:"-125px"}}>
              <h2>Active Community</h2>
              <p>WooCommerce is one of the fastest-growing eCommerce communities. </p>
              <a style={{textDecoration:"none"}} href="/">Check our Forums ›</a>
              </div>
          </Col>
      </Row>
     </Container>
    </div>
  );
}

export default Features;
