import React from 'react';
import { Container, Button } from 'react-bootstrap';
import photo1 from '../Images/Rectangle 236.png'
import photo2 from '../Images/Rectangle 233.png'
import Union from '../Images/Union.png'
import Ellipse31 from '../Images/Ellipse 31.png'
import Ellipse32 from '../Images/Ellipse 32.png'
import Ellipse34 from '../Images/Ellipse 34.png'
import group7 from '../Images/Group 7.png'
import group8 from '../Images/Group 8.png'

const Section = () => {
  return (
    <div className="section" style={{position:"relative", height:"150vh"}}>
      <Container>
        <div className='d-flex'>
          <div>
              <img style={{position:"relative", top:"-150px"}} src={Ellipse32} alt="" />
          <img style={{height:"548px", position:"relative", top:"-50px", zIndex:"2"}} src={photo1} alt="" />
            <img style={{position:"relative", top:"-400px", zIndex:"1"}} src={Ellipse31} alt="" />
          </div>
            <div style={{width:"480px", margin:"6rem 3rem", color:"white"}}>
                <div style={{position:"relative", left:"-275px"}}>
                <h2>
              Develop <br /> Without Limits
                </h2>
                <p>WooCommerce is developer friendly, too. Built with a REST API, WooCommerce is scalable and can integrate with virtually any service. Design a complex store from scratch, extend a store for a client, or simply add a single product to a WordPress site—your store, your way.</p>
                <Button style={{backgroundColor:"#70C217", borderRadius:"30px", padding:"1rem 2rem", borderColor:"#70C217"}}>
                    Read the Documentation
                </Button>
            </div>
              <img style={{position:"absolute", top:"0", right:"0"}} src={Union} alt="" />
            </div>
        </div>

        <div className="d-flex" style={{color:"white", position:"relative", top:"-500px"}}>
                <div className='d-flex' style={{position:"relative", top:"250px"}}>
                    <img style={{height:"400px", position:"relative", top:"-300px", paddingRight:"5rem"}} src={group7} alt="" />
                    <div style={{width:"480px"}}>
                    <h2>
                    Know our <br /> Global Community
                    </h2>
                    <p>WooCommerce is one of the fastest-growing eCommerce communities. We’re proud that the helpfulness of the community and a wealth of online resources are frequently cited as reasons our users love it. There are 80+ meetups worldwide!</p>
                    <Button style={{backgroundColor:"#70C217", borderRadius:"30px", padding:"1rem 2rem", borderColor:"#70C217"}}>
                    Read the Documentation
                    </Button>
                    </div>
                </div>

                <div className='d-flex'>
                
                <img style={{height:"548px", position:"relative", top:"150px", zIndex:"2"}} src={photo2} alt="" />
                <img style={{width:"300px", height:"162px", position:"relative", top:"400px", left:"-200px"}} src={group8} alt="" />
                <img style={{height:"262px", width:"262px", position:"relative", left:"-450px", top:"170px"}} src={Ellipse34} alt="" />
                <img style={{height:"62px", width:"62px", position:"relative", left:"-900px", top:"150px", zIndex:"3"}} src={Ellipse32} alt="" />
                </div>
            </div>
      </Container>
    </div>
  );
}

export default Section;
